@use "global" as *;



.p-new-articles {

}

.p-new-articles__title  {
  position: relative;
  padding-left: rem(34);
}
.p-new-articles__title::before  {
  position: absolute;
  content: "";
  background-image: url(../images/common/staff-clinic__icon02.svg);
  background-size: cover; //比率はそのままで￥拡張縮小、上下または左右が切り捨てになる。
  background-position: center; 
  background-repeat: no-repeat;
  width: rem(24);
  height: rem(24);
  left: 0;
}

.p-new-articles__items  {
  margin-top: rem(20);
}
